import anime from "animejs";
import { Mixins, Component } from "vue-property-decorator";
import util from "../lib/util";
import InitMixin from "../mixin/init";
import dayjs from "dayjs";
import jQuery from "jquery";
const GithubButton = require("vue-github-button").default;
const $: any = jQuery;
@Component({
    components: {
        GithubButton
    }
})
export default class Home extends Mixins(InitMixin) {
    mounted () {
        document.title = `Home -- ${this.customConfig.title}`;
        $(function () {
            $(".image").visibility({
                type: "image",
                transition: "vertical flip in",
                duration: 500
            });
        });
        util.init(true, true);

        $(".ui.borderless.network.secondary.menu").addClass("inverted");
        $(".club").attr("data-title", "点击访问GitHub")
            .popup({
                position: "top center",
                on: "hover"
            });

        $(".maintainer").attr("data-html", "<div class='header'>Ryan Lee</div><div class='content'>Maintainer & Developer</div>")
            .popup({
                position: "top center",
                on: "hover"
            });
        /*
        this.axios.get("/api/update_log/latest")
            .then((response) => {
                const data = response.data.data[0];
                const version = data.version;
                const vj_version = data.vj_version;
                const frontendVersion = data.frontend_version;
                const content = data.msg;
                const time = dayjs(data.mtime).format("YYYY-MM-DD");
                $(".maintain").html("Version:" + time).attr("data-html", "<div class='ui header'>" +
                    "升级维护内容" +
                    "<div class='sub header'>引擎版本:" + version +
                    "</div><div class='sub header'>VJ版本:" + vj_version +
                    "</div><div class='sub header'>前端版本:" + frontendVersion +
                        "</div></div><div class='content'>" + content + "</div>")
                    .popup({
                        position: "top center",
                        on: "hover"
                    });
            });

         */
        $(".support")
            .popup({
                position: "bottom center",
                inline: true,
                on: "hover",
                hoverable: true
            });
        $(".chrome").popup({
            title: "点击下载",
            position: "top center",
            on: "hover"
        });
        $(".generator").on("click", function () {
            $(".packer").transition("fade down");
            $(".vultr").transition("fade up");
        });
        $(".ml14 .letters").each(function (this: HTMLElement) {
            // eslint-disable-next-line no-control-regex
            $(this).html($(this).attr("data-content").replace(/([^\x00-\x80]|\w)/g, "<span class='letter'>$&</span>"));
        });

        // document.getElementById('myVideo').addEventListener('ended',false);

        window.picid = 5;
        this.axios.get("/api/login/").then(({ data }) => {
            const logined = data.logined;
            setTimeout(function () {
                $("#main_container").removeClass("unvisible");
                const maintainTime = anime.timeline({ loop: false })
                    .add({
                        targets: ".ml14 .line",
                        scaleX: [0, 1],
                        opacity: [0.5, 1],
                        easing: "easeInOutExpo",
                        duration: 900
                    }).add({
                        targets: ".ml14 .letter",
                        opacity: [0, 1],
                        translateX: [40, 0],
                        translateZ: 0,
                        scaleX: [0.3, 1],
                        textShadow: "3px 3px 3px #555555",
                        easing: "easeOutExpo",
                        duration: 800,
                        offset: "-=600",
                        delay: function (el: any, i: any) {
                            return 25 * +i;
                        }
                    });
                // eslint-disable-next-line no-constant-condition
                if (logined && false) {
                    maintainTime.add({
                        targets: "#main_masthead",
                        translateX: ["25%", 0],
                        easing: "easeInOutQuart",
                        duration: 1000,
                        offset: "-=300",
                        complete: function () {
                        }
                    }).add({
                        targets: "#right",
                        opacity: [0, 1],
                        easing: "easeInOutQuart",
                        duration: 1000,
                        offset: "-=300"
                    }).add({
                        targets: "#divider",
                        opacity: [0, 1],
                        easing: "easeInOutQuart",
                        duration: 500,
                        offset: "-=300",
                        complete: function () {
                            const $style = $("style");
                            let $html = $style.eq($style.length - 1).html();
                            $html += "\n.gr5::after{\nopacity:0.5;\n}\n";
                            $style.eq($style.length - 1).html($html);
                        }
                    });
                }
            }, 300);
        });
    }
}
